const apolloText = "To learn how GraphQL and Apollo work together, I followed a tutorial on Apollo's documentation guide." +
  "In this tutorial, I started with building a schema for my data and writing data fetching methods to a REST API. " +
  "To save data, I created a custom database for the apollo server to utilize. " +
  "Afterwards, I implemented GraphQL resolvers and added a pagination solution. " +
  "Lastly, I connected my React application with my custom Graph API with Apollo Client. " +
  "The end result is an appllication that can authenticate users, list available flights, and update a user's account with booked flights"
  ;

const identityText = "My first exposure to Machine Learning was a credit card scam detector that analyzed a spreadsheet of numbers. " +
  "After this exposure, I was interested in exploring the image processing side of Machine Learning. " +
  "I discovered there are many open source resources for ML image processing. " +
  "With the use of python libraries such as Numpy and face_reconition, I built a program which is able to detect faces on an image and label them with the appropriate name. "
  ;

const marioText = "As a child, I really enjoyed playing Super Mario Bros and Mario Kart on my Nitendo DS. " +
  "In 2016, I no longer had my DS so I tried downloading Super Mario Bros to my phone. " +
  "To my suprise, there was no option available. " +
  "I had been learning Android Studio for a couple months at that point and decided I wanted to build out a mobile version of Super Mario Bros. " +
  "This app was my first project that I spent considerable time on (100+ hours). " +
  "I accomplished a replica of old school Super Mario Bros with the physics library libGDX and the map editor Tiled. " +
  "My simplified version of Super Mario includes Goomba and Koopa as enemies. " + 
  "Mario's direction is driven by tilt controls and tapping for jumping. " +
  "I used sprites to give Mario and his enemies the illusion of moving and created three distinct levels with Tiled. " +
  "Interesting enough, a few months after I completed this app, an official mobile version of Super Mario Bros was released. " + 
  "Knowing how much work my simple version took, I was in awe at how well designed the official version was.";

